var self;
export default{
	onLoad() {
		self=this;
	},
	data(){
		return{
			array1:[
				"客厅","房间","厨房","厕所"
			],
			array2:[
				"插座","水管","电线"
			],
			array3:[
				"墙壁不平整","房间墙壁有脱皮"
			],
			index1:0,
			index2:0,
			index3:0,
			pic_index:0,
			pic:[],
			preview:[],
			del:[],
			wh:"",
			takenotes:[1],
			note:1
		}
	},
	methods:{
		changeMain1:(e)=>{
			self.index1=e.detail.value;
		},
		changeMain2:(e)=>{
			self.index2=e.detail.value;
		},
		changeMain3:(e)=>{
			self.index3=e.detail.value;
		},
		addPic:()=>{
			uni.showActionSheet({
			    itemList: ['图片', '视频'],
			    success: function (res) {
			        if(res.tapIndex==0){
						uni.chooseImage({
							count:5,
							sizeType: ['original', 'compressed'], 
							success: function (res) {
								res.tempFilePaths.forEach(function(e){
									if(self.pic_index>4){
										console.log("aa")
										uni.showToast({
											title:"最多选五张",
											icon:"none"
										})
										return false;
									}
									self.pic[self.pic_index]=e;
									self.pic_index++;
								})
								console.log(self.pic_index);
								console.log(self.pic);
							    }
						})
					}
					if(res.tapIndex==1){
						uni.chooseVideo({
							success:function(res){
								self.pic[self.pic_index]=res;
								self.pic_index++;
							}
						})
					}
			    }
			});
			
			
		},
		delPic:(index)=>{
			let j=self.pic_index-index;
			for(var i=0; i<j;i++){
				self.pic[index+i]=self.pic[index+i+1];
				self.pic.splice(index+i+1,0)
			}
			self.pic.pop();
			self.pic_index--;
		},
		previewPic:(index)=>{
			self.preview[0]=self.pic[index];
			uni.previewImage({
				urls:self.preview
			})
		},
		playVideo:()=>{
			self.wh="width: 750upx;height: 750upx;position: absolute; top:600upx;left:0;;z-index:20;"
			
		},
		endVideo:()=>{
			self.wh="";
		},
		takenotes_submit:(e)=>{
			console.log(e);
		},
		addnotes:()=>{
			self.note++;
			self.takenotes.push(self.note)
			console.log(self.takenotes)
		}
	}
}
